import React, { Component } from "react";

import "./HelpPage.css";

import { Link } from "react-router-dom";

import Adsense from "../../components/Adsense/Adsense.js";
import PageFrame from "../../components/PageFrame/PageFrame.js";
import Content from "../../components/Contents/index.js";
import Label from "../../components/Labels/Label/Label.js";
import ImageLabel from "../../components/Labels/ImageLabel/ImageLabel.js";
import HashTagSet from "../../components/HashTags/HashTagSet/HashTagSet.js";
import ImageViewer from "../../components/ImageViewer/ImageViewer.js";
import Switch from "../../components/Switch/Switch.js";

import VersionData from "../../components/Versions/VersionData.js";

import logoImage from '../../img/logo.png';

import installation_microsoftDefender from '../../img/helpPage/installation/microsoftDefender.png';
import installation_microsoftDefenderMoreInfo from '../../img/helpPage/installation/microsoftDefenderMoreInfo.png';
import installation_installation from '../../img/helpPage/installation/installation.png';

import liveDownload_mainMenu from '../../img/helpPage/liveDownload/mainMenu.png';
import liveDownload_channel from '../../img/helpPage/liveDownload/channel.png';

import videoDownload_mainMenu from '../../img/helpPage/videoDownload/mainMenu.png';
import videoDownload_channel from '../../img/helpPage/videoDownload/channel.png';
import videoDownload_videoList from '../../img/helpPage/videoDownload/videoList.png';

import clipDownload_mainMenu from '../../img/helpPage/clipDownload/mainMenu.png';
import clipDownload_channel from '../../img/helpPage/clipDownload/channel.png';
import clipDownload_clipList from '../../img/helpPage/clipDownload/clipList.png';


export default class HelpPage extends Component {
  constructor() {
    super();
    this.state = {
      hideHelpImages: false
    };
    this.changeImageDisplayState = (checked) => {
      this.setState({hideHelpImages: !checked});
    }
  }

  T(en, ko) {
    if (this.props.language === "en") {
      return en;
    }
    else {
      return ko;
    }
  }

  render() {
    return (
      <div className="HelpPage">
        <PageFrame>
          <Content.TitleBar>{this.T("Manual & Help", "사용법 & 도움말")}</Content.TitleBar>
          <Content.Area className="downloadLatestVersion" style={{padding: "30px"}}>
            <ImageLabel style={{padding: "15px", width: "200px", height: "200px"}} src={logoImage}/>
            <div className="infoArea" style={{marginLeft: "40px", width: "100%"}}>
              <div style={{display: "flex"}}>
                <Label style={{fontSize: "35pt"}}>TwitchLink</Label>
                <Label style={{marginTop: "30px", fontSize: "15pt", color: "gray"}}>{VersionData.latestVersion()}</Label>
              </div>
              <Label style={{fontSize: "15pt", color: "gray"}}>{this.T("Getting Started", "도움말")}</Label>
            </div>
          </Content.Area>
          <Adsense
            name="TwitchLink_Help_1"
            style={{"display": "block"}}
            dataAdSlot="9121104799"
            dataAdFormat="auto"
            dataFullWidthResponsive="true"
          />
          <Content.Area className="installation">
            <Label style={{display: "inline-block", margin: "0 0 0 10px", fontSize: "20pt"}}>{this.T("Install TwitchLink", "TwitchLink 설치")}</Label>
            <HashTagSet style={{margin: "0 0 5px 10px"}}>{this.T("#TwitchLink Installation", "#트위치링크 설치")}</HashTagSet>
            <Label style={{margin: "5px 0 20px 10px", color: "gray"}}>{this.T("How to install TwitchLink.", "TwitchLink 설치 방법")}</Label>
            <Content.Box>
              <Content.Subtitle>
                {this.T(
                  "1. TwitchLink Download",
                  "1. TwitchLink 다운로드"
                )}
              </Content.Subtitle>
              <Content.TextArea>
                {this.T(
                  "Download TwitchLink.",
                  "TwitchLink를 다운로드합니다."
                )}
                <Link className="LinkButton downloadButton" to={"/donate/?lang=" + this.props.language + "&download=" + VersionData.latestVersion()} target="_blank">
                  {this.T("Download TwitchLink", "TwitchLink 다운로드")}
                </Link>
              </Content.TextArea>
            </Content.Box>
            <Content.Box>
              <Content.Subtitle>
                {this.T(
                  "2. TwitchLink Installation",
                  "2. TwitchLink 설치"
                )}
              </Content.Subtitle>
              <Content.TextArea>
                {this.T(
                  "Run your TwitchLink Installer.",
                  "다운로드한 TwitchLink 설치파일을 실행합니다."
                )}
              </Content.TextArea>
              <Content.TextArea>
                {this.T(
                  "Click 'More info' and 'Run anyway' if Microsoft Defender appears.",
                  "만약 Microsoft Defender 화면이 표시되면 '추가 정보'를 클릭한 후 실행을 클릭합니다."
                )}
              </Content.TextArea>
              <ImageViewer hideImage={this.state.hideHelpImages} src={installation_microsoftDefender}/>
              <ImageViewer hideImage={this.state.hideHelpImages} src={installation_microsoftDefenderMoreInfo}/>
              <Content.TextArea>
                {this.T(
                  "Follow the instructions to proceed with the installation.",
                  "화면의 안내에 따라 설치를 진행합니다."
                )}
              </Content.TextArea>
              <ImageViewer hideImage={this.state.hideHelpImages} src={installation_installation}/>
            </Content.Box>
          </Content.Area>
          <Content.Area className="liveDownload">
            <Label style={{display: "inline-block", margin: "0 0 0 10px", fontSize: "20pt"}}>{this.T("Live Download", "생방송 다운로드")}</Label>
            <HashTagSet style={{margin: "0 0 5px 10px"}}>{this.T("#Download Twitch Live Stream #Live Recording", "#트위치 생방송 다운로드 #실시간 녹화")}</HashTagSet>
            <Label style={{margin: "5px 0 20px 10px", color: "gray"}}>{this.T("How to record and download Twitch Streams.", "트위치 생방송 실시간 다운로드 및 녹화 방법")}</Label>
            <Content.Box>
              <Content.Subtitle>
                {this.T(
                  "1. Channel Search",
                  "1. 채널 검색"
                )}
              </Content.Subtitle>
              <Content.TextArea>
                {this.T(
                  "Search for channels by channel ID or channel link.",
                  "채널 아이디 또는 채널 링크를 이용해 채널을 검색합니다."
                )}
              </Content.TextArea>
              <ImageViewer hideImage={this.state.hideHelpImages} src={liveDownload_mainMenu}/>
            </Content.Box>
            <Content.Box>
              <Content.Subtitle>
                {this.T(
                  "2. Download",
                  "2. 다운로드"
                )}
              </Content.Subtitle>
              <Content.TextArea>
                {this.T(
                  "If the channel is online, the Download button appears with the preview image.",
                  "채널이 온라인이라면 미리보기 이미지와 함께 실시간 다운로드 버튼이 표시됩니다."
                )}
              </Content.TextArea>
              <ImageViewer hideImage={this.state.hideHelpImages} src={liveDownload_channel}/>
              <Content.TextArea>
                {this.T(
                  "Click the button to download.",
                  "버튼을 눌러 실시간 다운로드를 진행합니다."
                )}
              </Content.TextArea>
            </Content.Box>
          </Content.Area>
          <Adsense
            name="TwitchLink_Help_2"
            style={{"display": "block"}}
            dataAdSlot="9433269445"
            dataAdFormat="auto"
            dataFullWidthResponsive="true"
          />
          <Content.Area className="videoDownload">
            <Label style={{display: "inline-block", margin: "0 0 0 10px", fontSize: "20pt"}}>{this.T("Video Download", "비디오 다운로드")}</Label>
            <HashTagSet style={{margin: "0 0 5px 10px"}}>{this.T("#Download Twitch Videos #Download Sub-Only Videos", "#트위치 다시보기 다운로드 #구독자 전용 다시보기 다운로드")}</HashTagSet>
            <Label style={{margin: "5px 0 20px 10px", color: "gray"}}>{this.T("How to download Twitch Videos, including Subscriber-Only Videos.", "트위치 다시보기 및 구독자 전용 다시보기 다운로드 방법")}</Label>
            <Content.Box>
              <Content.Subtitle>
                {this.T(
                  "1-A. Search Video in Channel",
                  "1-A. 채널에서 비디오 검색"
                )}
              </Content.Subtitle>
              <Content.TextArea>
                {this.T(
                  "Search for the channels and click the Video/Clip tab.",
                  "채널을 검색한 후 '비디오/클립' 탭을 클릭합니다."
                )}
              </Content.TextArea>
              <ImageViewer hideImage={this.state.hideHelpImages} src={videoDownload_channel}/>
              <Content.TextArea>
                {this.T(
                  "Select the type of video you want to search through the filter on the top right.",
                  "우측 상단의 필터를 통해 검색할 비디오의 종류를 선택합니다."
                )}
              </Content.TextArea>
              <ImageViewer hideImage={this.state.hideHelpImages} src={videoDownload_videoList}/>
              <Content.TextArea>
                {this.T(
                  "Find the video you want to download.",
                  "비디오 목록이 표시되면 다운로드할 비디오를 찾습니다."
                )}
              </Content.TextArea>
            </Content.Box>
            <Content.Box>
              <Content.Subtitle>
                {this.T(
                  "1-B. Search Video by Video ID",
                  "1-B. 비디오 아이디로 비디오 검색"
                )}
              </Content.Subtitle>
              <Content.TextArea>
                {this.T(
                  "Or search for videos by video ID or video link.",
                  "또는 비디오 아이디 또는 비디오 링크를 통해 비디오를 검색합니다."
                )}
              </Content.TextArea>
              <ImageViewer hideImage={this.state.hideHelpImages} src={videoDownload_mainMenu}/>
            </Content.Box>
            <Content.Box>
              <Content.Subtitle>
                {this.T(
                  "2. Download",
                  "2. 다운로드"
                )}
              </Content.Subtitle>
              <Content.TextArea>
                {this.T(
                  "The download button appears with the preview image.",
                  "미리보기 이미지와 함께 다운로드 버튼이 표시됩니다."
                )}
              </Content.TextArea>
              <Content.TextArea>
                {this.T(
                  "Click the button to download.",
                  "버튼을 눌러 다운로드를 진행합니다."
                )}
              </Content.TextArea>
            </Content.Box>
          </Content.Area>
          <Content.Area className="clipDownload">
            <Label style={{display: "inline-block", margin: "0 0 0 10px", fontSize: "20pt"}}>{this.T("Clip Download", "클립 다운로드")}</Label>
            <HashTagSet style={{margin: "0 0 5px 10px"}}>{this.T("#Download Twitch Clips", "#트위치 클립 다운로드")}</HashTagSet>
            <Label style={{margin: "5px 0 20px 10px", color: "gray"}}>{this.T("How to download Twitch Clips.", "트위치 클립 다운로드 방법")}</Label>
            <Content.Box>
              <Content.Subtitle>
                {this.T(
                  "1-A. Search Clip in Channel",
                  "1-A. 채널에서 클립 검색"
                )}
              </Content.Subtitle>
              <Content.TextArea>
                {this.T(
                  "Search for the channels and click the Video/Clip tab.",
                  "채널을 검색한 후 '비디오/클립' 탭을 클릭합니다."
                )}
              </Content.TextArea>
              <ImageViewer hideImage={this.state.hideHelpImages} src={clipDownload_channel}/>
              <Content.TextArea>
                {this.T(
                  "Select 'Clips' from the filter on the top right.",
                  "우측 상단의 필터에서 '클립'을 선택합니다."
                )}
              </Content.TextArea>
              <ImageViewer hideImage={this.state.hideHelpImages} src={clipDownload_clipList}/>
              <Content.TextArea>
                {this.T(
                  "Find the clip you want to download.",
                  "클립 목록이 표시되면 다운로드할 클립을 찾습니다."
                )}
              </Content.TextArea>
            </Content.Box>
            <Content.Box>
              <Content.Subtitle>
                {this.T(
                  "1-B. Search Clip by Clip ID",
                  "1-B. 클립 아이디로 클립 검색"
                )}
              </Content.Subtitle>
              <Content.TextArea>
                {this.T(
                  "Or search for clips by clip ID or clip link.",
                  "또는 클립 아이디 또는 클립 링크를 통해 클립을 검색합니다."
                )}
              </Content.TextArea>
              <ImageViewer hideImage={this.state.hideHelpImages} src={clipDownload_mainMenu}/>
            </Content.Box>
            <Content.Box>
              <Content.Subtitle>
                {this.T(
                  "2. Download",
                  "2. 다운로드"
                )}
              </Content.Subtitle>
              <Content.TextArea>
                {this.T(
                  "The download button appears with the preview image.",
                  "미리보기 이미지와 함께 다운로드 버튼이 표시됩니다."
                )}
              </Content.TextArea>
              <Content.TextArea>
                {this.T(
                  "Click the button to download.",
                  "버튼을 눌러 다운로드를 진행합니다."
                )}
              </Content.TextArea>
            </Content.Box>
          </Content.Area>
        </PageFrame>
        <div className="imageToggler">
          <div style={{margin: "5px 10px"}}>
            {this.T("Show Images", "이미지 표시")}
          </div>
          <Switch isChecked={!this.state.hideHelpImages} handleToggle={this.changeImageDisplayState}/>
        </div>
      </div>
    );
  }
}